<template>
  <div>
    <van-sticky>
      <van-nav-bar title="交易密码" left-arrow @click-left="onClickLeft" />
    </van-sticky>
    
    <div class="userWrap">
      <van-cell-group>
    <van-field label="手机号" placeholder="请输入手机号" v-model="userInfo.phone"  />
    <!-- <van-field label="验证码" placeholder="请输入验证码" v-model="userInfo.pid_member"  /> -->
    <van-cell-group>
  <van-field
    v-model="userInfo.verificationCode"
    center
    clearable
    label="验证码"
    placeholder="请输入验证码"
  >
    <van-button slot="button" size="small" type="primary"  @click="send()" :disabled='!disabled'>{{codetext}}</van-button>
  </van-field>
</van-cell-group>
    <van-field label="新密码" type='password' placeholder="请输入新密码" v-model="userInfo.t_password"  />
    <van-field label="确认新密码" type='password' placeholder="请确认新密码" v-model="userInfo.t_a_password"  />
      </van-cell-group>
      <button class="submitBtn" @click="updateUser">提交</button>
    </div>
  </div>
</template>

<script>
import { userInfo, uploadImg, updateUser,sender,editSecondPassword } from "@/api/personal";
export default {
  data() {
    return {
             disabled: true,
        codetext:"获取",
      userInfo: {
        phone: "",
        t_a_password: "",
        t_password: "",
        verificationCode: ""
      },
      img: ""
    };
  },
  created() {
    this.getUser();
  },
  methods: {
           countDown (time) {
        if (time === 0) {
          this.disabled = true
          this.codetext = "获取"
          return
        } else {
          this.disabled = false;
          this.codetext = '重新发送(' + time + ')'
          time--
        }
        setTimeout(()=> {
          this.countDown(time)
        }, 1000)
      },
        send() {
            if(this.userInfo.phone==undefined){
          this.$toast("请输入手机号码!");
           return false
            }
            sender({phone:this.userInfo.phone}).then(res=>{
             this.countDown(60)
            })
    },
    getUser() {
    //   userInfo().then(res => {
    //     this.userInfo = res;
    //     this.userInfo.sex = this.userInfo.sex == 1 ? "男" : "女";
    //   });
    },
    onClickLeft() {
      this.$router.go(-1);
    },
    upImg(file) {
      this.image = file.content.substring(file.content.indexOf(",") + 1);
      uploadImg(this.image, "").then(resp => {
        this.userInfo.wechat_img = resp.img;
      });
    },
    // 提交
    updateUser() {
      editSecondPassword(this.userInfo).then(res => {
          if(res.errcode==0){
          this.$toast("修改成功!");
          this.userInfo={}
          }else{
          this.$toast(res.msg);
          }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.van-button--primary{
     background: white;
     color: #28A6FF;
     border: 1px solid #28A6FF;
     border-radius: 20px;
}
.van-cell {
  padding: 15px 16px 19px;
}
.userImg {
  width: 39px;
  height: 39px;
}
.submitBtn {
  display: block;
  width: 345px;
  height: 40px;
  line-height: 40px;
  color: #fff;
  font-size: 18px;
  border-radius: 20px;
  background: #28A6FF;
  margin: 66px auto;
  border: 0 none;
}
</style>
